import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader, 
    AlertDialogTitle,
  } from "@/components/ui/alert-dialog";

import React, { Dispatch, SetStateAction } from 'react';
   
function DeleteConfirmationDialog( { dialogOpen, setDialogOpen}:{ dialogOpen:boolean, setDialogOpen: Dispatch<SetStateAction<boolean>>} ) {

   
    return (
      <AlertDialog open={dialogOpen} onOpenChange={setDialogOpen}> 

        <AlertDialogContent>

          <AlertDialogHeader>
            <AlertDialogTitle>Delete Clash</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to Delete this clash ?
            </AlertDialogDescription>
          </AlertDialogHeader>

          <AlertDialogFooter>
            <AlertDialogCancel >Cancel</AlertDialogCancel>
            <AlertDialogAction>Delete</AlertDialogAction>
          </AlertDialogFooter>

        </AlertDialogContent>

      </AlertDialog>
    )
  }

export default DeleteConfirmationDialog;














