"use client";
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
  } from "@/components/ui/dropdown-menu";
  
  
  import { JSX, useState } from "react";
import DeleteConfirmationDialog from "./DeleteConfirmationDialog";
  
  
  
  
  function CardMenu( { children}:{ children:JSX.Element}) {

    const [ deleteDialogOpen, setDeleteDialogOpen] = useState(false);

    return (
      <DropdownMenu>
        { children}
        <DropdownMenuContent className="w-40 mr-2">
  
            <DropdownMenuItem>
              Edit
            </DropdownMenuItem>
            
            <DropdownMenuItem>
              Copy Link
            </DropdownMenuItem>

            <DropdownMenuItem onClick={()=>setDeleteDialogOpen(true)}>
              Delete 
            </DropdownMenuItem>
  
        </DropdownMenuContent>
        
        {/* delete dialog confirmation */}
        <DeleteConfirmationDialog dialogOpen={deleteDialogOpen} setDialogOpen={setDeleteDialogOpen} />
  
      </DropdownMenu>
    )
  }
  
  export {  CardMenu};
  
  
  
  
  
  
  
  
  
  
  
  
  